import React from 'react';
import "./endzone-alert.css";
import { Alert } from '@mui/material';
import IconButton from '@mui/material/IconButton';
import CloseIcon from '@mui/icons-material/Close';

class EndzoneAlert extends React.Component {
    constructor (props) {
        super(props);
        this.state = {
            alertDisplay: "none",
            severity: "info",
            userMessage: "" 
        }
    }

    closeAlert = () => {
        this.setState({
            alertDisplay: "none",
            severity: "info",
            userMessage: "" 
        });
    }

    openAlert = (message, severity) => {
        this.setState({
            alertDisplay: "block",
            severity: severity,
            userMessage: message
        })
    }

    handleRequestError = (response) => {
        try {
            console.log(response)
            this.setState({
                alertDisplay: "block",
                severity: "error",
                userMessage: response.response.data
            })
        }
        catch {
            this.setState({
                alertDisplay: "block",
                severity: "error",
                userMessage: "Unexpected Error - Please Contact: endzone.support@gmail.com"
            })
        }
    }

    render() {
        return(
            <div className='alert-div' style = {{display: this.state.alertDisplay}}>
                <Alert variant="filled" severity={this.state.severity}
                            action={
                                <IconButton
                                aria-label="close"
                                color="inherit"
                                size="small"
                                onClick={() => {this.closeAlert()}}
                                >
                                <CloseIcon fontSize="inherit" />
                                </IconButton>
                            }>
                            {this.state.userMessage}
                </Alert>
            </div>
        );
    }
}



export default EndzoneAlert;