import React, { Component } from "react";
import { default as ReactSelect } from "react-select";
import "./endzone-multiselect.css";
import { components } from "react-select";

const Option = (props) => {
  return (
    <div>
      <components.Option {...props}>
        <input type="checkbox" checked={props.isSelected} onChange={() => null}/>{" "}
        <label>{props.label}</label>
      </components.Option>
    </div>
  );
};

export default class EndzoneMultiSelect extends Component {
  constructor(props) {
    super(props);
    this.state = {
      optionSelected: []
    };
  }

  handleChange = (selected) => {
    console.log(selected)
    this.setState({
      optionSelected: selected
    });
    this.props.handleChange(selected);
  };

  render() {
    const sortedOptions = this.props.data.sort((a, b) => {
        try {
          a.label.localeCompare(b.label)
        } catch {
          console.log('Sorting Error') 
        }
      } 
    );

    return (
      <div className="endzone-multiselect-div">
        <span class="d-inline-block" data-toggle="popover" data-trigger="focus">
            <ReactSelect
              options={sortedOptions}
              isMulti
              closeMenuOnSelect={false}
              hideSelectedOptions={true}
              components={{Option}}
              onChange={this.handleChange}
              allowSelectAll={true}
              value={this.state.optionSelected}
              id = {this.props.id}
            />
        </span>
      </div>
    );
  }
}