import React from 'react';

import EndzoneNav from '../../../components/navigators/endzone-nav/EndzoneNav'
import EndzoneMultiSelect from '../../../components/selectors/endzone-multiselect/endzone-multiselect';
import EndzoneSelect from '../../../components/selectors/endzone-select/endzone-select';
import EndzoneAlert from '../../../components/misc-items/endzone-alert/endzone-alert'

import "./model-builder.css"
import "./dialog.css"

import Dialog from '@mui/material/Dialog';
import DialogActions from '@mui/material/DialogActions';
import DialogContent from '@mui/material/DialogContent';
import DialogTitle from '@mui/material/DialogTitle';

import DeleteOutlineIcon from '@mui/icons-material/DeleteOutline';
import HelpOutlineIcon from '@mui/icons-material/HelpOutline';
import PsychologyIcon from '@mui/icons-material/Psychology';
import CircularProgress from '@mui/material/CircularProgress';


import { IconButton, Tooltip, Button } from '@mui/material';
import LoadingButton from '@mui/lab/LoadingButton';
import AddIcon from '@mui/icons-material/Add';
import axios from 'axios';
import { DataGrid} from '@mui/x-data-grid';



class ModelBuilder extends React.Component {
    constructor (props) {
        super(props);
        this.state = {
            modelData: [],
            gameData: [],
            isCreateModelOpen: false,
            game_objects: [],
            team_options: [],
            game_options: [],
            selected_team: [],
            model_focus: null,
            model_loading: false,
            activeModelName: "Select a Model from the Table",
            activeModelID: "",
            modelOutputText: ""
        }
        this.newModel = {generalizeFeatures: false}
        this.predictionParams = {
            Yard: 10,
            Hash: 'Left',
            Down: 1,
            Distance: 10
        }
        this.dataGrid_columns = [
            { field : 'Activate', width : 100, renderCell:(cellValues)=>{
                return(
                    <Button variant = "contained" color ="primary" sx={{fontSize: '10px', fontFamily: 'Audiowide', backgroundColor: 'black'}} onClick = {(e)=>{
                        let id = cellValues.row.id
                        this.activateModel(id, cellValues.row.ModelName)
                    }}>Activate</Button>
                )}
            },
            { field : "ModelName", headerName : "Model Name", flex : 1, editable: true}, 
            { field : "ModelType", headerName : "Focus", flex : 1, editable: false},
            { field: 'ModelClass', headerName: 'Class', flex : 1, editable: false},
            { field: 'ModelPredictor', headerName: 'Predicts', flex : 1, editable: false, valueGetter : (params) => (String(params.row.ModelPredictor).replace('_', ' ').replace(/([A-Z])/g, ' $1').trim())},
            { field: 'ModelAccuracy', headerName: 'Accuracy',  flex : 1, editable: false, valueGetter : (params) => ((parseFloat(params.row.ModelAccuracy).toFixed(2)) * 100) + "%"},
            { field: 'ModelTrainingSize', headerName: 'Plays used to Simulate', flex : 1, editable: false},
            { field : "Creation_Date", headerName : "Creation Date", flex : 1, valueGetter : (params) => new Date(params.row.Creation_Date).toDateString(), width : 300},
            { field : 'Delete', width : 100, renderCell:(cellValues)=>{
                return(
                    <IconButton onClick = {(e)=>{
                        let id = cellValues.row.id
                        this.deleteModel(id, cellValues.row.ModelName)}}>
                        <DeleteOutlineIcon></DeleteOutlineIcon>
                    </IconButton>
                )}
            },
        ];
        this.alertRef = React.createRef()
    }

    activateModel = (id, modelName) => {this.setState({activeModelID: id, activeModelName: modelName})} 

    componentDidMount = () => {
        this.getModels()
        this.getGames()
    }

    handleMultiSelectChange = (selections) => {
        // Flattened list of select object
        let selected_data_list = []
        selections.map((selection) => {
            selected_data_list.push(selection.value)
        })
        return selected_data_list
    }

    getModels = () => {
        axios.get('/endzone/models/get').then((response) => {
            this.setState({modelData: response.data})
        }, (error) => {
            this.alertRef.current.handleRequestError(error);
            this.setState({modelData: []})
            console.log(error)
        })
    }

    getGames = () => {
        axios.get('/endzone/data/game/get').then((response) => {
            this.loadGames(response.data)  
            this.setState({game_data: response.data})
          }, (error) => {
            this.alertRef.current.handleRequestError(error);
            //To-Do display error
            this.loadGames([])
            this.setState({game_data: []})
            
          })
      }

    convertSingularListToSelectDict = (list) => {
        let updated_list = []
        list.map((elm) => {
            updated_list.push({value: elm, label: elm})
        }) 
        return updated_list
    }

    addModel = () => {
        this.newModel.TeamOfInterest = this.state.selected_team[0];
        this.newModel.ModelType = this.state.model_focus;

        // Check Input Parameters
        if(this.newModel.TeamOfInterest === "" || this.newModel.TeamOfInterest == undefined){this.alertRef.current.openAlert("A Team of Interest is Required.", "warning"); return;}
        else if(this.newModel.GameIDs == undefined || this.newModel.GameIDs.length == 0){this.alertRef.current.openAlert("Please Select Games to Use.", "warning"); return;}
        else if(this.newModel.ModelType === "" || this.newModel.ModelType == undefined){this.alertRef.current.openAlert("A Model Focus is Required.", "warning"); return;}
        else if(this.newModel.ModelPredictor === "" || this.newModel.ModelPredictor == undefined){this.alertRef.current.openAlert("A Model Predictor is Required.", "warning"); return;}
        else if(this.newModel.PredictionParams == undefined || this.newModel.PredictionParams.length == 0){this.alertRef.current.openAlert("Please Select Attributes to Use as Predictors.", "warning"); return;}
        else if(this.newModel.ModelClass === "" || this.newModel.ModelClass == undefined){this.alertRef.current.openAlert("A Model Type is Required.", "warning"); return;}
        else if(this.newModel.ModelName === "" || this.newModel.ModelName == undefined){this.alertRef.current.openAlert("A Model Name is Required.", "warning"); return;}

        this.alertRef.current.closeAlert()

        this.setState({model_loading: true});
        axios.post(
            "/endzone/models/create", this.newModel
        ).then((response) => {
            this.getModels()
            this.setState({model_loading: false})
        }, (error) => {
            this.alertRef.current.handleRequestError(error);
            this.setState({model_loading: false})
        })
    }

    deleteModel = (modelId, modelName) => {
        if(window.confirm(`Are you sure you want to delete ${modelName}?`))
        {
            axios.post(
                "/endzone/models/delete", {modelID: modelId}
            ).then((response) => {
                this.getModels()
            }, (error) => {
                this.alertRef.current.handleRequestError(error);
            })
        }
    }

    filterGames = () => {
        let game_objects = this.state.game_objects;
        let filtered_objects = [];
        let selected_team = this.state.selected_team;
  
        
        game_objects.map((game_object) => {
            if((selected_team.length == 0  || selected_team.includes(game_object.Away_Team) || selected_team.includes(game_object.Home_Team))) {
                filtered_objects.push({value: game_object.Game_ID, label: game_object.gameLabel})
            }
            
        })
        this.setState({game_options: filtered_objects})
    }

    loadGames = (game_data) => {
        let teams_of_interest_setList = new Set()
        let years_setList = new Set()
        let game_object = []
    
        game_data.map((game) => {
            console.log(game)
            if(!teams_of_interest_setList.has(game.Away_Team)) {   
                teams_of_interest_setList.add(game.Away_Team)
            }
            if(!teams_of_interest_setList.has(game.Home_Team)) {
                teams_of_interest_setList.add(game.Home_Team)
            }
            let game_date = new Date(game.Game_Date)
            let year = game_date.getUTCFullYear()
            if(!years_setList.has(year)){
                years_setList.add(year)
            }
    
            let game_string = game.Home_Team + " vs " + game.Away_Team + " - " + game_date.toDateString()
            game_object.push({gameLabel: game_string, Home_Team: game.Home_Team, Away_Team: game.Away_Team, Year: game_date.getUTCFullYear(), Game_ID: game.Game_ID})
    
        }) 
            this.setState({
                team_options: this.convertSingularListToSelectDict([...teams_of_interest_setList]),
                game_objects: game_object
            }, () => {
                this.filterGames()
            })
    }

    writeToOutput(text) {
        let currentIndex = 0;
        const interval = setInterval(() => {
          if (currentIndex === text.length) {
            clearInterval(interval);
          } else {
            const newOutputText = this.state.modelOutputText + text.charAt(currentIndex);
            this.setState({modelOutputText: newOutputText });
            currentIndex++;
          }
        }, 1);
    }

    getPrediction = () => {
        let data = {
            modelID: this.state.activeModelID,
            predictionParams: this.predictionParams
        }
        axios.post("/endzone/models/predict", data).then((response) => {
            this.setState({modelOutputText: ''}, () => {
                this.writeToOutput(this.state.activeModelName + " | " + response.data)
            })
        }, (error) => {
            console.log(error)
        })
        
    }

    render() {
        return(
            <div className='model-builder-page'>
                <EndzoneAlert ref={this.alertRef}></EndzoneAlert>
                <div>
                    <EndzoneNav links={[{title: "Return to Hub", link: "/endzone/pregame/hub"},]}></EndzoneNav>
                </div>
                <div className='model-builder-container'>                    
                    <div className='model-builder-div'>
                        <div className='model-builder-header-div'>
                            <div className='model-builder-header'>
                                <div className='model-builder-header-row'>
                                    <h2>Model Builder</h2>
                                    <Tooltip title="Create new Model" arrow={true} placement='right'>
                                        <IconButton>
                                            <AddIcon sx={{color: 'white', border: '2px solid #262830', borderRadius: '50%'}} onClick={() => this.setState({isCreateModelOpen: true})}></AddIcon>
                                        </IconButton>
                                    </Tooltip>
                                </div>
                                <div className='model-builder-header-row'>
                                    <p>Create and Manage Prediction models for in-game and pregame usage.</p>
                                    <Tooltip title="Support" arrow={true} placement='right'>
                                        <IconButton>
                                            <HelpOutlineIcon sx={{color: '#929499'}}></HelpOutlineIcon>
                                        </IconButton>
                                    </Tooltip>
                                </div>
                            </div>
                        </div>

                        <div className='model-builder-body'>
                                <DataGrid className = 'data-grid' style = {{"color" : "black","font-size":"1.85vh","backgroundColor" : 'white', "width" : "100%", "border": "1px solid #0984e3", "fontFamily":"Audiowide", "margin": '1px'}} rows={this.state.modelData} columns={this.dataGrid_columns}></DataGrid>                             
                            <div className='make-predictions-div'>
                                <div className='active-model'>
                                    <h4>{this.state.activeModelName}</h4>
                                </div>
                                <div className='make-predictions-body'>
                                    <div className='make-predictions-form'>
                                        <div className='form-element'>
                                            <h4>Yard:</h4>
                                            <Tooltip title={"Input Yard Line"} arrow={true} placement='right'>
                                                <input onChange={(e) => this.predictionParams.Yard = e.target.value} style={{width: '40px'}} max={100} min={0} type='number'></input>
                                            </Tooltip>
                                        </div>
                                        <div className='form-element'>
                                            <h4>Down:</h4>
                                            <Tooltip title={"Input Down"} arrow={true} placement='right'>
                                                <input onChange={(e) => this.predictionParams.Down = e.target.value} style={{width: '40px'}} max={4} min={0} type='number'></input>
                                            </Tooltip>
                                        </div>
                                        <div className='form-element'>
                                            <h4>Distance:</h4>
                                            <Tooltip title={"Input Distance"} arrow={true} placement='right'>
                                                <input onChange={(e) => this.predictionParams.Distance = e.target.value} style={{width: '40px'}} max={100} min={1} type='number'></input>
                                            </Tooltip>
                                        </div>
                                        <div className='form-element'>
                                        <h4>Hash:</h4>
                                            <Tooltip title={"Input Hash"} arrow={true} placement='right'>
                                                <select onChange={(e) => this.predictionParams.Hash = e.target.value}>
                                                    <option>Left</option>
                                                    <option>Middle</option>
                                                    <option>Right</option>
                                                </select>
                                            </Tooltip>
                                        </div>
                                        <div className='form-element' style={{justifyContent: 'right', margin: '0px'}}>
                                            <Tooltip title={"Get Prediction"} arrow={true} placement='right'>
                                                <IconButton>
                                                    <PsychologyIcon onClick = {() => this.getPrediction()} sx={{color: 'white', border: '2px solid #0984e3', borderRadius: '50%', fontSize: '120%'}}></PsychologyIcon>
                                                </IconButton>
                                            </Tooltip>
                                        </div>
                                    </div>
                                    <div className='make-predictions-output'>
                                        <p>{this.state.modelOutputText}</p>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
                <div>
                    <Dialog open={this.state.isCreateModelOpen} onClose={() => this.setState({isCreateModelOpen: false})}>
                        <DialogTitle sx={{fontFamily: "Audiowide", backgroundColor: "#202128" , color: "white", textDecoration: "underline"}}>New Model</DialogTitle>
                        <DialogContent sx={{backgroundColor: "#202128"}}>
                            <div className='create-model-div'>
                                <div className='form-element'>
                                    <Tooltip title={"Team you would like to predict attributes on"} arrow={true} placement='right'>
                                        <h4>Team to Study:</h4>
                                    </Tooltip>
                                    <EndzoneSelect data={this.state.team_options} handleChange = {(selection) => this.setState({selected_team: this.handleMultiSelectChange(selection)}, () => this.filterGames())}></EndzoneSelect>
                                </div>
                                <div className='form-element'>
                                    <Tooltip title={"Games you would like to base predictions on: Pick most representative of your opponent"} arrow={true} placement='right'>
                                        <h4>Games to Study:</h4>
                                    </Tooltip>
                                    <EndzoneMultiSelect data={this.state.game_options} handleChange = {(selection) => this.newModel.GameIDs = this.handleMultiSelectChange(selection)}></EndzoneMultiSelect>
                                </div>
                                <div className='divider-div'></div>
                                
                                <div className='form-element'>
                                    <Tooltip title={"What type of prediction model do you want?"} arrow={true} placement='right'>
                                        <h4>Model Focus:</h4>
                                    </Tooltip>
                                    <EndzoneSelect 
                                        data={[{'label': 'Offense', 'value': 'Offense'}, {'label': 'Defense', 'value': 'Defense'}]}  handleChange = {(selection) => this.setState({model_focus: selection[0].value})}>    
                                    </EndzoneSelect>
                                </div>

                                <div className='form-element'>
                                    <Tooltip title={"What you'd like to predict"} arrow={true} placement='right'>
                                        <h4>Attribute to Predict:</h4>
                                    </Tooltip>
                                    <EndzoneSelect 
                                        data={this.state.model_focus == 'Offense' ? [{'label': 'Play Type', 'value': 'Play_Type'}] : [{'label': 'Coverage', 'value': 'Coverage'}, {'label': 'Pressure', 'value': 'Pressure'}]}
                                        handleChange = {(selection) => this.newModel.ModelPredictor = selection[0].value}
                                    >
                                    </EndzoneSelect>
                                </div>
                                <div className='form-element' style={{display: 'inline-block'}}>
                                    <Tooltip title={"Would you like to generalize predictions to improve accuracy? Example: Zone 1 and Zone 2 become Zone"} arrow={true} placement='right'>
                                        <h4>Generalize Predictions:</h4>
                                    </Tooltip>
                                    <input className='check-input' type={"checkbox"} onChange={(e) => this.newModel.generalizeFeatures = e.target.checked}></input>
                                </div>
                                <div className='form-element'>
                                    <Tooltip title={"Factors to base predictions on."} arrow={true} placement='right'>
                                        <h4>Factors to Consider:</h4>
                                    </Tooltip>
                                    <EndzoneMultiSelect data={
                                        this.state.model_focus == 'Offense' ? 
                                        [
                                            {'label': 'Down', 'value': 'Down'},
                                            {'label': 'Yard', 'value': 'Yard'},
                                            {'label': 'Hash', 'value': 'Hash'},
                                            {'label': 'Distance', 'value': 'Distance'},
                                        ] 
                                        : 
                                        [
                                            {'label': 'Down', 'value': 'Down'},
                                            {'label': 'Yard', 'value': 'Yard'},
                                            {'label': 'Hash', 'value': 'Hash'},
                                            {'label': 'Distance', 'value': 'Distance'},
                                        ]
                                    }
                                    handleChange = {(selection) => this.newModel.PredictionParams = this.handleMultiSelectChange(selection)}
                                    ></EndzoneMultiSelect>
                                </div>
                                <div className='form-element'>
                                    <Tooltip title={"Method to create predictions. Read more in support section."} arrow={true} placement='right'>
                                        <h4>Model Type:</h4>
                                    </Tooltip>
                                    <EndzoneSelect 
                                        data={[{'label': 'Simulation Based', 'value': 'RF'}, {'label': 'Grouping Based', 'value': 'KNN'}, {'label': 'Occurence Based', 'value': 'TARS'}]}
                                        handleChange = {(selection) => this.newModel.ModelClass = selection[0].value}>
                                    </EndzoneSelect>
                                </div>
                                <div className='form-element'>
                                    <Tooltip title={"Identifying name for model."} arrow={true} placement='right'>
                                        <h4>Model Name:</h4>
                                    </Tooltip>
                                    <input maxLength={35} className='text-input' type='text' onChange={(e) => this.newModel.ModelName = e.target.value}></input>
                                </div>
                            </div>
                        </DialogContent>
                        <DialogActions sx={{backgroundColor: "#202128"}}>
                            <LoadingButton 
                                loadingIndicator = {<CircularProgress size={'24px'}></CircularProgress>}
                                loading = {this.state.model_loading} 
                                onClick={() => this.addModel()} sx={{fontFamily: "Audiowide", color: "white", borderBottom: '1px solid #0984e3'}}>Add Model</LoadingButton>
                            <Button onClick={() => this.setState({isCreateModelOpen: false})} sx={{fontFamily: "Audiowide", color: "white"}}>Cancel</Button>


                        </DialogActions>
                    </Dialog>
                </div>
            </div>
        );
    }
}



export default ModelBuilder;